.btn-clear {
    width: 12px;
    height: 12px;
    position: relative;
    &::before,
    &::after {
        content: '';
        position: absolute;
        width: 9px;
        height: 1px;
        border-radius: 2px;
        background-color: var(--text-color);
        top: calc(50% - 0.1rem);
        left: calc(50% - .6rem);
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }
}
